const loading = (
  <div className="pt-3 text-center">
    <div className="sk-spinner sk-spinner-pulse"></div>
  </div>
)

class ReserverTerrainPourClient extends Component {
  constructor(props) {
      super(props)
      this.state = {
        ok:"",
      }
  }
  
  componentDidMount() {
  };


  render() {

    return (
      <>
        {this.state.ok}
        <CContainer fluid>
          <Suspense fallback={loading}>
            <CFade>
              <div className="mt-5"></div>
              <section id="contact" class="contact">
                <div class="container">
                ReserverTerrainPourClient
                </div>
              </section>
            </CFade>
          </Suspense>
        </CContainer>
      </>
    )
  }
}

export default ReserverTerrainPourClient
